/* Background Animation */
@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.icon-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.icon-button svg {
  font-size: 18px;
}

.App {
  background: linear-gradient(-45deg, #696f2f, #222009, #949435, #716610);
  background-size: 400% 400%;
  animation: gradientAnimation 20s ease infinite;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-container {
  width: 400px;
  background-color: rgb(101, 102, 96);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  overflow: hidden;
  position: relative;
}

.toggle-panel {
  display: flex;
  justify-content: space-around;
  background-color: rgb(39, 41, 32); /* Dark purple color */
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.toggle-button {
  width: 50%;
  padding: 10px 0;
  cursor: pointer;
  border: none;
  color: white;
  background-color: #253826;
  transition: background-color 0.3s;
}

.toggle-button:hover {
  background-color: #042c0d;
}

.toggle-button.active {
  background-color: #240224;
}

.form-container {
  padding: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.login-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}

.login-form h2 {
  margin-bottom: 20px;
  color: white;
  text-align: center;
}

.login-form input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid rgb(53, 61, 15);
  border-radius: 20px;
  box-sizing: border-box;
}

.login-form button {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  background-color: #232d23;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
}

.login-form button:hover {
  background-color: #000000;
}

