/* Google Button Styling */
.google-button {
  background-color: #4285F4;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-radius: 20px;
  margin-bottom: 10px;
  cursor: pointer;
  border: none;
  width: 100%;
  text-align: center;
  font-size: 16px;
  font-family: inherit; /* Ensure font family matches other buttons */
}

.google-button svg {
  margin-right: 8px; /* Add space between icon and text */
  font-size: 18px; /* Match the icon size with Facebook icon */
}

.google-button:hover {
  background-color: #357AE8;
}
